body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.App {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.App-header {
	text-align: center;
}

.container {
	width: 100%;
	max-width: 1020px;
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: #f0f0f0;
	padding-top: 20px;
}

.PostBlock {
	width: 640px;
	border-radius: 8px;
	background-color: white;
	margin-bottom: 20px;
}

.PostHeader {
	font-size: 15px;
}

.PostInfo {
	padding: 16px 20px 0px;
	display: flex;
	justify-content: flex-start;
}

.PostInfo > * {
	margin-right: 8px;
}

.PostInfo__subsite {
	font-size: 16px;
	font-weight: 500;
}

.PostInfo__subsite-logo {
	display: block;
	width: 22px;
	height: 22px;
	background-repeat: no-repeat;
	background-size: contain;
	border-radius: 6px;
}

.PostInfo__rating {
	text-align: right;
	color: #2ea83a;
	font-size: 16px;
	font-weight: 500;
	flex: 2;
}

.PostHeader__title {
	margin: 9px 0px 7px;
	padding: 0px 20px;
	font-size: 22px;
	font-weight: 500;
}

.PostHeader__discription {
	padding: 0px 20px;
	margin-bottom: 6px;
}

.PostContent {
	/*padding: 30px 25px;*/
	display: flex;
	justify-content: center;
	border-radius: 8px;
}

.portrait {
	padding: 30px 25px;
}

.portrait > * {
	background-position: center;
}

.PostBlock:hover {
	/* border: 2px solid red; */
	box-shadow: 0 2px 24px rgb(0 0 0 / 56%);
}
